export default {
	data() {
		return {
			imgUrl: this.$imgUrl,
			info: [],
			current: 0,
			spotNewsList: [],
		};
	},
	onLoad:function(option){
		
	},
	mounted() {
		this.gettingData()
		this._getNewsBannerList()
	},
	methods: {
		// 获取banner图
		_getNewsBannerList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_news_banner_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.info = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		gettingData () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_news_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.spotNewsList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		change (e) {
			this.current = e.detail.current;
		},
	}
}
